@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-backface-visibility: hidden;
  -webkit-user-drag: none;
  /*font-family: "Microsoft YaHei", "微软雅黑", Helvetica, Tahoma,Arial, SimSun, "宋体", STXihei, "华文细黑", Heiti, "黑体", sans-serif;*/
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  /* 字体抗锯齿 */
  cursor: pointer; }

html, body {
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif; }

a {
  text-decoration: none; }

em {
  font-style: normal; }

input, textarea {
  margin: 0;
  padding: 0;
  border: none;
  outline: none; }

input[type=number] {
  -moz-appearance: textfield; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset; }

::-webkit-input-placeholder, ::-webkit-textarea-placeholder {
  margin: 0;
  padding: 0;
  color: #8c8c8c; }

@media screen and (min-width: 1600px) {
  html, body {
    font-size: 22px; } }

@media screen and (min-width: 1400px) and (max-width: 1600px) {
  html, body {
    font-size: 20px; } }

@media screen and (min-width: 1200px) and (max-width: 1400px) {
  html, body {
    font-size: 18px; } }

@media screen and (min-width: 992px) and (max-width: 1200px) {
  html, body {
    font-size: 17px; } }

@media screen and (min-width: 768px) and (max-width: 992px) {
  html, body {
    font-size: 17px; } }

@media screen and (max-width: 768px) {
  html, body {
    font-size: 17px; } }

/* 定义适配基础单位 */
/* 定义px转rem规则 */
/* 定义间距尺寸 */
:root {
  --head-top: #f5f5f5;
  --head-tab: #ffffff;
  --head-name: #141414;
  --head-tips: #262626; }

.finance-box .finance-firm {
  margin: 0 auto;
  margin-top: 1.75rem;
  width: 55.5rem;
  display: flex;
  flex-direction: row; }
  .finance-box .finance-firm .firm-image {
    flex: 4;
    margin-top: 1.75rem;
    margin-right: 1.125rem;
    border-radius: 3px;
    overflow: hidden; }
  .finance-box .finance-firm .firm-box {
    flex: 3; }
    .finance-box .finance-firm .firm-box .firm-title {
      margin: 0 auto;
      margin-top: 1.75rem;
      font-size: 0.875rem;
      font-weight: 400;
      color: #141414;
      text-indent: 2em; }
    .finance-box .finance-firm .firm-box .firm-tips {
      margin: 0 auto;
      margin-top: 1.125rem;
      font-size: 0.875rem;
      font-weight: 400;
      color: #141414;
      text-indent: 2em;
      color: #cf1322;
      font-weight: 500; }

.finance-box .finance-choose {
  margin: 0 auto;
  margin-top: 1.75rem;
  margin-bottom: 1.125rem;
  width: 55.5rem;
  display: flex;
  flex-direction: row;
  border-top: 1px solid #d9d9d9; }
  .finance-box .finance-choose .finance-article {
    margin-top: 1.125rem; }
    .finance-box .finance-choose .finance-article .article-head p {
      margin-top: 0.5rem;
      font-size: 1rem;
      font-weight: 400;
      color: #141414;
      color: #cf1322;
      font-weight: 500;
      text-align: center; }
    .finance-box .finance-choose .finance-article .article-body {
      margin-top: 1.125rem;
      font-size: 0.875rem;
      font-weight: 400;
      color: #141414;
      text-indent: 2em; }

.finance-box .finance-over {
  margin: 0 auto;
  margin-bottom: 1.75rem;
  margin-top: 1.75rem;
  border-top: 1px solid #d9d9d9;
  width: 55.5rem; }
  .finance-box .finance-over h1 {
    margin-top: 4.25rem;
    font-size: 1.375rem;
    font-weight: 500;
    color: #262626;
    text-align: center;
    margin-top: 1.125rem; }
  .finance-box .finance-over .over-tips {
    margin-top: 1.125rem;
    font-size: 0.875rem;
    font-weight: 400;
    color: #141414;
    text-indent: 2em; }
  .finance-box .finance-over .over-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between; }
    .finance-box .finance-over .over-list .over-item {
      margin-top: 1.75rem; }
      .finance-box .finance-over .over-list .over-item .image-box {
        border-radius: 3px;
        overflow: hidden; }
      .finance-box .finance-over .over-list .over-item div {
        margin-top: 0.5rem;
        font-size: 0.875rem;
        font-weight: 400;
        color: #141414;
        text-align: center; }
